.landing {
    height: 100vh;
    background: linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.2)), url('../assets/images/img.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center; 
    display: flex;
    flex-direction: column;

}

.landing  h1 {
    color: white;
    padding-top: 1em;
    padding-left: 2em;
    font-size: 4em;
}

.landing p {
    color: white;
    padding-top: 2em;
    padding-left: 4em;
    font-size: 2em;
}